@import url("https://fonts.googleapis.com/css2?family=Libre+Bodoni:ital,wght@0,400..700;1,400..700&display=swap");

.App {
  text-align: center;
  background-color: #fdf4f1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App-header {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: calc(100% - 80px);
  height: 100px;
  border-bottom: 1px solid #000;
  text-align: center;
}

.linkBox {
  width: calc(100% - 80px);
  border-bottom: 1px solid #000;
  padding: 6px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.linkBox > span {
  font-family: "Libre Bodoni", serif;;
  color: black;
  text-decoration: none;
  font-weight: bold;
  font-size: 26px;
}

.main {
  max-width: 1920px;
  height: calc(100vh - 145px);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}


.mainImgL {
  position: relative;
}

.mainImgL img {
  width: 400px;
  height: 580px;
  position: relative;
  z-index: 10;
}

.plusBlock {
  width: 300px;
  height: 450px;
  background-color: #dd4a37;
  position: absolute;
  top: -50px;
  right: -110px;
}

.mainImgTxt {
  font-family: "Libre Bodoni", serif;;
  font-size: 70px;
  line-height: 100%;
}
.mainImgTxt.top {
  position: absolute;
  top: -13%;
  margin: 0;
  left: -100%;
  width: 700px;
}
.mainImgTxt.btm {
  position: absolute;
  bottom: -13%;
  margin: 0;
  right: -100%;
  width: 600px;
}
.mainImgTxt > li {
  display: inline-block;
  overflow-y: hidden;
}
.mainImgTxt > .point {
  font-family: 'Oswald-Bold';
  font-weight: bold;
  font-size: 85px;
  height: 90px;
}

/* profile */
.plusTxt {
  font-family: 'Nanum Myeongjo';
  text-align: center;
  font-size: 24px;
  margin: 40px 0;
}

/* recently */
.swiperArea {
  width: calc(100% - 80px);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  position: relative;
}

.swiperTitle {
  width: 35%;
  min-width: 550px;
  position: relative;
  padding: 0 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.swiperTitle p {
  font-family: "Libre Bodoni", serif;;
  font-size: 70px;
  font-weight: bold;
  text-align: left;
  position: absolute;
  top: -115px;
  background-color: #fdf4f1;
}
.swiper {
  width: 65%;
  overflow: hidden;
}

.workDia {
  font-family: 'Nanum Myeongjo';
  font-weight: 500;
  font-size: 40px;
  position: relative;
  display: inline-block;
  width: max-content;
  margin: 0 auto;
  font-style: italic;
  color: #888;
  text-align: center;
}
.openQuote {
  position: absolute;
  top: -50px;
  left: -60px;
  opacity: 0.2;
}
.closeQuote {
  position: absolute;
  bottom: -40px;
  right: -50px;
  opacity: 0.2;
}

.workSlide {
  border-left: 1px solid #000;
}
.workCard a {
  display: block;
}
.workCard img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.workDetail {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.7);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}
.theaterName {
  font-size: 24px;
  margin: 0px 0px 4px;

}
.workDate {
  font-size: 20px;
}
.showTitle {
  font-size: 24px;
  color: #fff;
  text-align: left;
}


.btnBox {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: absolute;
  width: calc(100% - 40px);
  bottom: 15px;
}

/* history */
.workBox {
  width: 90%;
}
.workBoxTitle {
  font-family: "Libre Bodoni", serif;;
  font-size: 50px;
  font-weight: bold;
  margin: 0;
}
.workBoxIn {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 0px;
  margin: 0;
}
.workPosterBox {
    width: 100%;
    position: relative;
    overflow: hidden;
    margin: 0 auto;
}
.workPosterBox::after {
  content: "";
  display: block;
  clear: both;
}
.workImg {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 2;
}
.historyDetail {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 0 8px;
}
.year.on {
  text-decoration: line-through;
}

/* 방명록 */
.guestBox {
  width: calc(100% - 80px);
  max-width: 1200px;
  margin: 0 auto;
  height: calc(100vh - 80px);
}
.guestTxt {
  font-family: 'Noto Sans Korean';
}
.guestBtn {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.likeBtn {
  background-color: unset;
  vertical-align: text-top;
  border: none;
}

.guestInputBox {
  box-sizing: border-box;
  padding: 10px;
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.guestInputBox > input[type="text"] {
  width: 100%;
  border: 1px solid #dcdcdc;
  border-radius: 3px;
  height: 100%;
  margin-right: 20px;
}
.writeBtn {
  font-family: 'Noto Sans Korean';
  width: 80px;
  background-color: #dd4a37;
  font-size: 15px;
  text-align: center;
  border: none;
  color: #fff;
  height: 100%;
  border-radius: 3px;
}

.guestList {
  padding-left: 0;
}
.guestList > li {
  padding: 0 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style-type: none;
  align-items: center;
  border-bottom: 1px solid #e1e1e1;
}
